@import "navigation";


@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


* {
  box-sizing: border-box;
  padding: 0;
  margin:0
}

body {
  font-family: 'Montserrat', sans-serif;
  color: #333;
  line-height: 1.6;
}


.navbar .flex {
  justify-content: space-between;
}

.container {
    max-width: 1800px;
    margin: 0 auto;
    overflow: auto;
    padding: 0 40px; 
  }
  
  .flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }
  
  .navbar ul {
    display: flex;
    align-items: center;
    }
  
  .navbar a {
    padding: 10px;
    margin: 0 5px;
    transition: font-size 0.3s, color 0.2s;
  }
  
  .navbar a:hover {
    color: #ff4599;
    font-size: 1.15em;
  }
  
  ul.nav > li:last-child {
    background-color: #ff4599;
    padding: 5px;
    border-radius: 20px;
  }
  
  .createTask > button {
    font-family: 'Montserrat', sans-serif;
    font-size: 1em;
    outline: none;
    border: none;
    background: transparent;
    color:white;
  }

  /* TOP NAV BAR */
.navBarLeft {
    display: flex;
    align-items: center;
  }
  
  header {
    position: sticky;
    left: 0;
    top: 0;
    background-color: #e3e3e3;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    overflow: auto;
  }
  
  header > .navBarLeft > img {
    width: 100px;
    height: 100px;
  }
  
  i	{
    margin: 2rem;
  }
  
  i:hover {
    color: #383838;
    cursor: pointer;
  }
  
  header > .navBarLeft > button {
    background: none;
    border: none;
    margin: none;
    padding: none;
  }
  
  .createTask {
    margin: 2rem;
  }
  
  .createTask {
    transition: font-size 0.3s, color 0.2s;
    background-color: #ff4599;
    padding: 0.5rem;
    border-radius: 20px;
    border: none;
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-size: 1em;
  }
  
  .createTask:hover {
    background-color: #ee3a8b;
    font-size: 1.1em;
    cursor: pointer;
  }

  /* SIDEBAR */

.mainContentContainer {
    display: flex; 
  }
  
  .sidebar {
    width: 90px;
    height: 100%;
    display: flex;
    padding-top: 2em;
    flex-direction: column;
    flex-shrink: 0;
    align-items: center;
  }
  
  .sidebar > a > img {
    margin: 0.5rem;
    width: 80px;
    height: 80px;
    
  }
  
  
  .sidebar > a > img:hover {
    background: #f2f2f2;
    border-radius: 1em;
    cursor: pointer;
  }
  
  
  /* Kanban Board */
  
  .kanbanBoardWrapper {
    display: grid;
    grid-auto-flow: column;
    overflow-x: auto;
  }
  
  .taskColumn {
    display: flex; 
    flex-direction: column;
    flex-flow: column wrap;
    align-content: center;
    
  }
  
  .taskColumn > div {
    padding: 1em;
    margin-left: 1.5em;
    margin-right: 1.5em;
  }
  
  .statusHolder {
    display: inline-block;
  }
  
  a.button {
    background: #6868df;
    padding: 0.3em 1em;
    border-radius: 1em;
    font-size: 0.8em;
    color: white;
    text-decoration: none;
  }
  
  .kanbanBoardWrapper > div:nth-child(odd) {
    background: #f5f5f5;
  }
  
  
  .taskBox {
    background: #ffffff;
    padding: 1rem;
    border: 3px solid #9f91e3;
    border-radius: 1.5em;
    width: 24rem;
    display: inline-block;
    margin-bottom: 1.5em;
    cursor: move;
  }
  
  
  .taskBox > p.assignmentID {
    font-size: 1em;
    color: #aaaaf3;
    font-weight: 600;
    
  }
  
  .taskBox > h1 {
    font-size: 1.5em;
  }
  
  .taskBox > p.taskDescription {
    color: #4a4a4a;
    padding: 4px 0 4px 0;
  }
  
  .taskBox > ul {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .taskBox.dragging {
    opacity: 0.6;
  }
  
  .dueDate {
    color: grey;
    list-style-type: none;
  }
  
  .unitCode {
    background: #aaaaf3;
    padding: 0.3em 1em;
    border-radius: 2em;
    color: white;
    font-size: 0.8em
  }

  /* Create Task Popup */
.modal {
    position: fixed;
    z-index: 99;
    top: 50%;
    left: 50%;
    background: white;
    transform: translate(-50%, -50%) scale(0);
    width: 300px;
    height: 350px;
    padding: 2rem;
    border-radius: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .modal.active {
    transform: translate(-50%, -50%) scale(1);
  }
  
  .modal form {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
  }
  
  .modal > form > h1 {
    font-size: 1.5em;
    padding: 1.5rem;
    text-align: center;
    font-weight: 700;
  }
  
  
  .modal > form >  button:nth-last-of-type(-n+2):hover {
    background-color: #dedede;
    cursor: pointer;
  }
  
  form input {
    border: none;
    padding: 0.5rem;
    width: 100%;
    margin: 0.3rem;
    border-bottom: 1px solid black;
    outline: none;
    font-size: 1em;
    font-family: "Montserrat", sans-serif;
    
  }
  
  ::placeholder {
    color: #bfbfbf;
  }
  
    /* make background blurry */
  #overlay {
    position: absolute;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(2px) brightness(80%);
    opacity: 0;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
  }
  
  #overlay.active {
    opacity: 1;
    pointer-events: all;
  }
  
  button img {
    width: 2.2em;
    height: 2em;
    padding: 0.4rem;
  }
  
  
  .closeButton {
    background: none;
    border: none;
  }
  
  .closeButton:hover {
    cursor: pointer;
    color: #ededed;
    border-radius: 50%;
  }
  

  /* DICTIONARY */
.dictionaryContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  
  .dictionaryContainer h1 {
    margin: 1rem;
  }
  
  #definitionsContainer {
    background: #EEF2FA;
    width: 25rem;
    height: auto;
    margin: 3rem;
    padding: 1rem 2rem;
    border-radius: 20px;
  }

  
  .synonymsContainer {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: space-around; 
    
    
  }
  
  .synonymsContainer > p {
    background: white;
    margin: 0.2rem;
    padding: 0.05rem 0.7rem;
    border-radius: 1rem;
  }

  /* MUSIC PLAYER STYLING */

.playlistRowContainer {
    padding-left: 30px;
    padding-top: 30px;
    padding-bottom: 30px;
    display: flex;
    flex-direction: column;
    overflow: auto;
    
  }
  
  .playlistRow {
    display: grid;
    grid-auto-flow: column;
    overflow-x: auto;
    justify-content: start;
    width: 100%;
  }
  
  .playlistRowContainer h2 {
    padding-top: 1.5rem;
    padding-bottom: 0.4rem;
    font-size: 1.1rem;
    color: #424242;
  }
  
  .playlistCover {
    padding: 1rem;
    margin: 1rem;
    width: 18rem;
    height: 11rem;
    border-radius: 1rem;
    color: white;
    display: flex;
    align-items: flex-end;
    font-weight: 300;
    transition: filter 0.2s;
    
  }
  
  .playlistCover:hover {
    cursor: pointer;
    /* filter: brightness(70%); */
  }
  
  .playlistCover > h3 {
    animation-name: textRiseReverse;
    animation-duration: 0.2s;
    padding-left: 0;
  }
  
  
  .playlistCover:hover > h3 {
    animation-name: textRise;
    animation-duration: 0.2s;
    animation-fill-mode: forwards;
    animation-play-state: revert;
  }
  
  .playlistCover > h3 > img {
    opacity: 0%;
    transition: opacity 0.2s;
  }
  
  .playlistCover:hover > h3 > img {
    opacity: 100%
  }
  
  
  .playlistCover.hogwarts {
    background-image: url(https://images.unsplash.com/photo-1618944913480-b67ee16d7b77?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1470&q=80);
    background-size: cover;
  }
  
  .playlistCover.bardcore {
    background-image: url(https://external-preview.redd.it/Z6wvktf259wvoR_fUTN6KEk-NNlbf7GDncWH86sqnac.jpg?auto=webp&s=af7c135d328929d09bd02c765a4e934dcdbdc913);
    background-size: cover;
  }
  
  .playlistCover.studioGhibli {
    background-image: url(https://mymodernmet.com/wp/wp-content/uploads/2021/01/studio-ghibli-history-7.jpg);
    background-size: cover;
  }
  
  .playlistCover.witcher {
    background-image: url(https://c4.wallpaperflare.com/wallpaper/985/837/295/the-witcher-the-witcher-3-wild-hunt-kaer-morhen-geralt-of-rivia-hd-wallpaper-preview.jpg);
    background-size: cover;
  }
  
  
  .playlistCover.rain {
    background-image: url(https://images.unsplash.com/photo-1496034663057-6245f11be793?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80);
    background-size: cover;
  }
  
  .playlistCover.lofi {
    background-image: url(https://i.ytimg.com/vi/tgI6PjEq0O8/maxresdefault.jpg);
    background-size: cover;
  }
  
  .playlistCover.fireplace {
    background-image: url(https://images.unsplash.com/photo-1538250934773-edd3e8e185c0?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1470&q=80);
    background-size: cover;
  }
  
  
  @keyframes textRise{
    from {
      padding-bottom: 0; 
    }
  
    to {
      padding-bottom: 10px;
    }
  }
  
  @keyframes textRiseReverse{
    from {
      padding-bottom: 10px;
    }
    
    to {
      padding-bottom: 0;
    }
  }
  
  .musicController {
    width: 30px;
    height: 30px;
  }
  
  h3 {
    display: flex;
    align-items: center;
  }


  /* TIMERS */
.wrapper {
    margin: auto;
  }
  
  .wrapper h1 {
    text-align: center;
    padding: 1rem;
  }
  
  .timerContainer {
    display: flex; 
    flex-direction: column;
    align-items: center;
    margin: 20px;
    padding: 4rem;
    border-radius: 3rem;
    box-shadow: 0px 2px 5px #cfcfcf;
  }
       
  .timerContainer.pomodoro {
    outline: 5px solid #F8E4E4;
  }
  
  .timerContainer.stopwatch {
    outline: 5px solid #F8E4E4;
  }
  
  
  .timerContainer h2 {
    text-align: center;
  }
  
  .timerDisplay  {
    padding: 2rem 1.5rem;
    background-color: #f5f5f5;
    border-radius: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .timerDisplay p {
   font-size: 3em;
   font-weight: 700;  
  }
  
  .timerDisplay.pomodoro p {
    color: #F36666;
  }
  
  .timerDisplay.stopwatch p {
    color: #4865C4;
  }
  
  .controlsContainer {
    display: flex;
    flex-direction: row;
  }
  
  .controlsContainer button {
    margin: 1rem;
    border: none;
    padding: 0.3rem 0.7rem;
    font-family: 'Montserrat', sans-serif;
    border-radius: 3rem;
    font-size: 1rem;
    cursor: pointer;
    background-color: white;
  }
  
  .pomodoro > .controlsContainer button {
    color: #F36666;
   }
  
  .stopwatch > .controlsContainer button {
    color: #4865C4;
  }
  
  /*Mobile Formatting */
  
  .sidebarContainer.toggleOn {
    display: inline;
  }
  
  @media screen and (max-width: 799px) {
    .sidebarContainer {
      display:none;
    }
  }